.envelope {
	width: 600px;
	height: 350px;
	position: relative;
	background: #435d77;
	border-radius:0 0 20px 20px;
}

.left, .right, .top {
  width: 0;
  height: 0;
  position:absolute;
  top:0;
  z-index: 310;
}

.left {	
	border-left: 300px solid #fff;
	border-top: 160px solid transparent;
	border-bottom: 160px solid transparent;
}
.right {	
	border-right: 300px solid #fff;
	border-top: 160px solid transparent;
	border-bottom: 160px solid transparent;
	left:300px;
}

.top {	
	border-right: 300px solid transparent;
	border-top: 200px solid #f1e6ee;
	border-left: 300px solid transparent;
	-webkit-transition: border 1s, ease-in-out, -webkit-transform 1s;
	transition: border 1s, ease-in-out, -webkit-transform 1s;
	-o-transition: transform 1s, border 1s, ease-in-out;
	transition: transform 1s, border 1s, ease-in-out;
	transition: transform 1s, border 1s, ease-in-out, -webkit-transform 1s;
	-webkit-transform-origin: top;
	    -ms-transform-origin: top;
	        transform-origin: top;
	-webkit-transform: rotateX(180deg);
	        transform: rotateX(180deg);
	border-top: 200px solid #2c3e50;
	z-index: 200;
}

.bottom {
	width: 600px;
	height: 190px;
	position: absolute;
	background: #fff;
	top: 160px;
	border-radius: 0 0 20px 20px;
	z-index: 310; 
}

